@import "~materialize-css/dist/css/materialize.min.css";

body {
  margin: 0;
  padding: 0;
}

.todo {
  margin-bottom: 1rem;
}

.todo span {
  color: black;
}

.todo.completed span {
  color: gray;
  text-decoration: line-through;
}

.todo label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  background: #f3f3f3;
  padding: 1rem;
}

.todo i {
  font-size: 20px;
  color: red;
}

